<template>
    <div class="tabbar">
        <router-view></router-view>

        <van-tabbar v-model="activeName" active-color="#dc3620" inactive-color="grey" route>
            <van-tabbar-item v-for="(item,index) in tabItemsData" :name="item.routerName"
            replace :to="item.routerPath"
            :key="index">
                <span> {{item.title}}</span>
                <template #icon="props">
                    <img :src="props.active ? item.icon_active : item.icon" />
                </template>
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>

    import '../assets/font/iconfont.css'
    export default {
        data(){
            return {
                activeName: 'Home',
                tabItemsData: [
                    {
                        title: '首页',
                        routerName: 'Home',
                        routerPath: '/tabBar/home',
                        icon: require('../assets/images/index.png'),
                        icon_active: require('../assets/images/index-active.png')
                    },
                    {
                        title: '菜单',
                        routerName: 'Menu',
                        routerPath: '/tabBar/menu',
                       icon: require('../assets/images/menu.png'),
                        icon_active: require('../assets/images/menu-active.png')
                    },
                    {
                        title: '购物车',
                        routerName: 'ShopCar',
                        routerPath: '/tabBar/shopCar',
                       icon: require('../assets/images/shopcar.png'),
                        icon_active: require('../assets/images/shopcar-active.png')
                    },
                    {
                        title: '我的',
                        routerName: 'Mine',
                        routerPath: '/tabBar/mine',
                        icon: require('../assets/images/my.png'),
                        icon_active: require('../assets/images/my-active.png')
                    }
                ]
            }
        },
        methods: {
            goState(name){
                this.$router.push(name);
            }
        },
    }
</script>
